import React, {
  useState,
  ReactNode,
  FC
} from 'react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: 'sm' | 'md' | 'lg';
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size,
  onClick
}: ButtonProps) => {

  return (
    <button
      onClick={onClick}
      style={{
        height: '50px',
      }}
    >
      {children}
    </button>
  )
}

export default Button;
